$(document).ready(function () {
  AOS.init();
  // -----------------  Маска для телефона ----------------------------
  $(".form-phone").mask("+375 (999) 999 999", );
  // ------------------------------------------------------------------
  var lastScrollTop = 0;
  $(window).bind('scroll', function () {
    var menu = 50;
    if ($(window).scrollTop() > menu) {
      $('.header').addClass('scroll');
      $('.catalog').removeClass('active');
      $('.banner__body').addClass('scroll');
    } else if (($(window).scrollTop() == 0)) {
      $('.header').removeClass('scroll');
      $('.banner__body').removeClass('scroll');
      if ($('.header').hasClass('active')) {
        $('.catalog').addClass('active');
      }

    }
    var st = $(this).scrollTop();

    if (st > lastScrollTop) {
      $('.header').removeClass('up');
    } else {
      // upscroll code
      $('.header').addClass('up');
    }
    lastScrollTop = st;
  });


  $(document).mouseup(function (e) { // событие клика по веб-документу

    var sel = $('.select.is-active');
    if (!sel.is(e.target) // если клик был не по нашему блоку
      &&
      sel.has(e.target).length === 0) { // и не по его дочерним элементам
      sel.removeClass('is-active'); // скрываем его
    }
  });
  $(document).mouseup(function (e) { // событие клика по веб-документу
    var div = $(".catalog"); // тут указываем ID элемента
    var sel = $('.select');
    if (!div.is(e.target) // если клик был не по нашему блоку
      &&
      div.has(e.target).length === 0) { // и не по его дочерним элементам
      div.removeClass('active'); // скрываем его
    }
  });
  // -----------------  Счетчик цифр анимированный-----------------------
  var s;
  $({
    countNum: $('.partner__sum').text()
  }).animate({
    countNum: 554652
  }, {
    duration: 2500,
    easing: 'linear',
    step: function () {
      var s = Math.floor(this.countNum).toString().replace(/(\d)(?=(\d{3})+(\D|$))/g, '$1 ');
      $('.partner__sum').text(s);
    },
    complete: function () {
      $('.partner__sum').text(s);

    }
  });
  // -----------------------------------------------------------------------------------------

});

$('.catalog__head').click(function () {
  $('.catalog').toggleClass('active');
});
$('.catalog').mouseleave(function () {
  $(this).removeClass('long');
});
$('.catalog').mouseover(function () {
  $(this).addClass('long');
});
var catalog_id = 0;
$('.catalog__content li').mouseover(function () {
  $('.catalog__content li').removeClass('active');
  $(this).addClass('active');
  catalog_id = $(this).find("a").attr('data-id');
  $('.submenu').removeClass('active');
  $('#' + catalog_id).addClass('active');
  $('#' + catalog_id).mouseover(function () {
    $(this).addClass('active');
  });
});
var sub_id = 0;
$('.submenu__list li').mouseover(function () {
  $('.submenu__list li').removeClass('active');
  $(this).addClass('active');
  sub_id = $(this).find("a").attr('data-id');
  $('.submenu__brend').removeClass('active');
  $('#' + sub_id).addClass('active');
  $('#' + sub_id).mouseover(function () {
    $(this).addClass('active');
  });
});

$('.quick__close').click(function () {
  $(this).parent().removeClass('active');
  $('.card').removeClass('active');
});
$('.js-quick').click(function () {
  $('.card').removeClass('active');
  $('.quick').removeClass('active');
  var quick_id = $(this).attr('data-quick');
  $(".quick[data-quick-id='" + quick_id + "']").addClass('active');
  $(this).parent().parent().parent().addClass('active');
});


$('.catalog').mouseleave(function () {
  $('.submenu').removeClass('active');
  $(this).find('li').removeClass('active');
});

$('.catalog').mouseleave(function () {
  $('.subbrend').removeClass('active');
});

$('.table__link').click(function () {
  var c = $(this).attr('href').replace('#', '');
  $('.table__row').removeClass('active');
  $('#' + c).addClass('active');
});

// ----------------------------------------------------------------------------------------------------
const allcity_btn = document.querySelector('.point__allcity-close');
const allcity = document.querySelector('.point__allcity');
const allcity_show = document.querySelector('.point__all-city');
if (allcity_show) {
  allcity_show.addEventListener('click', function (e) {
    if (allcity) {
      allcity.classList.add('active');
    }
  })
}
if (allcity_btn) {
  allcity_btn.addEventListener('click', function (e) {
    allcity.classList.remove('active');
  })
}


$('.gift-slide__option-btn').click(function () {
  $(this).parent().siblings().children().removeClass('active');
  $(this).addClass('active');
});


